import React, { Component, PropTypes } from 'react';

class Step4 extends Component {
  constructor(props){
    super(props);
    this.state = {
      sizing: false,
    };
  }

  toggle() {
    this.setState({
      sizing: !this.state.sizing
    });
  }
  render() {
    return(
      <div className='quiz-page__step quiz-page__step--4'>
        <span className='quiz-page__step__header header1--mobile'>Which shape best represents your body type?</span>
        <div className='quiz-page__step__content '>
        <div className='quiz-page__step__row'>
          <div onClick={() => this.props.onNext()}>
            <span className='text--small--bold'>Hourglass</span>
            <img src={require('../../../images/quiz/Hourglass.svg')} alt='Hourglass' />
          </div>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>Triangle</span>
          <img src={require('../../../images/quiz/Triangle.svg')} alt='Triangle' />
          </div>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>Rectangle</span>
          <img src={require('../../../images/quiz/Rectangle.svg')} alt='Rectangle' />
          </div>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>Round</span>
          <img src={require('../../../images/quiz/Round.svg')} alt='Round' />
          </div>
        </div>
        <div className='quiz-page__step__row'>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>Pear</span>
          <img src={require('../../../images/quiz/Pear.svg')} alt='Pear' />
          </div>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>Inverted Triangle</span>
          <img src={require('../../../images/quiz/Inverted Triangle.svg')} alt='Inverted Triangle' />
          </div>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>Diamond</span>
          <img src={require('../../../images/quiz/Diamond.svg')} alt='Diamond' />
          </div>
        </div>
        </div>
      </div>
    );
  }
}

Step4.propTypes = {
  onNext: PropTypes.object.isRequired
};

export default Step4;


