import React , { Component } from 'react';
import { Link } from 'react-router';

import Step1 from './quizSteps/Step1';
import Step2 from './quizSteps/Step2';
import Step3 from './quizSteps/Step3';
import Step4 from './quizSteps/Step4';
import Step5 from './quizSteps/Step5';
import Step6 from './quizSteps/Step6';
import Step7 from './quizSteps/Step7';
import Step8 from './quizSteps/Step8';

class QuizPage extends Component {
  constructor(props){
    super(props);
    this.state = {
      current: 1,
    };
    this.previousStep = this.previousStep.bind(this);
    this.nextStep = this.nextStep.bind(this);
  }

  previousStep() {
    this.setState({
      current: this.state.current - 1,
    });
  }

  nextStep() {
    this.setState({
      current: this.state.current + 1,
    });
  }

  renderStep() {
    switch(this.state.current) {
      case 1:
        return (<Step1 onNext={this.nextStep}/>);
      case 2:
        return (<Step2 onNext={this.nextStep}/>);
      case 3:
        return (<Step3 onNext={this.nextStep}/>);
      case 4:
        return (<Step4 onNext={this.nextStep}/>);
      case 5:
        return (<Step5 onNext={this.nextStep}/>);
      case 6:
        return (<Step6 onNext={this.nextStep}/>);
      case 7:
        return (<Step7 onNext={this.nextStep}/>);
      case 8:
        return (<Step8 onNext={() => console.log("SUCCESS")}/>);
      default:
        null;
    }
  }
  render() {
    const currentState = this.state.current;
    return (
      <div className='quiz-page'>
        <div className='quiz-page__header'>
          {
            currentState !== 1?
              <span className='quiz-page__header--left' onClick={() => this.previousStep()}>&#8592;</span>
              :
              null
          }
          <span className='quiz-page__header--center  text--small'>{currentState} <span>of</span> 8</span>
          <Link to='/' className='quiz-page__header--right'>&times;</Link>
        </div>
        <div className='quiz-page__content'>
            {this.renderStep()}
        </div>
      </div>
    );
  }
}

export default QuizPage;


