import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { joinWaitlist, onAuth } from './landingActions';
import LandingPage from '../../components/LandingPage';
import AuthPage from '../../components/AuthPage';

export class LandingContainer extends Component {

	render(){
		if (this.props.isAuthenticated){
			return (
				<LandingPage onWaitlistClick = {this.props.onWaitlistClick} />
			);
		} else {
			return (
				<AuthPage onAuthClick = {this.props.onAuthClick} />
			);
		}
	}
}

LandingContainer.propTypes = {
	onWaitlistClick	: PropTypes.func.isRequired,
	onAuthClick	: PropTypes.func.isRequired,
	isAuthenticated	: PropTypes.bool.isRequired
};

const mapStateToProps = (state) => {
	const { isAuthenticated, errorMessage } = state.landingReducer;
	return {
		isAuthenticated,
		errorMessage
	};
};

const mapDispatchToProps = (dispatch) => {
	return{
		onWaitlistClick: bindActionCreators(joinWaitlist, dispatch),
		onAuthClick: bindActionCreators(onAuth, dispatch),
		dispatch
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(LandingContainer);



