import React from 'react';
import { Link } from 'react-router';

const OrderSuccess = () => {
		return (
			<div className='order-success-page'>
					<div className='double-border' />
					<div className='order-success-page__container'>
						<div>
							<span className='header1--mobile'>Thank you for your order!</span>
							<span className='tagline'>Confirmation email sent.</span>
							<Link to='/browse' className='button navigation-text'>Back to browse</Link>
						</div>
					</div>
			</div>
		);
};

export default OrderSuccess;


