import { WAITLIST_SUCCESS, WAITLIST_FAILURE, AUTH_SUCCESS } from './landingActions';

const landingReducer = ( state = { isFetching: false, isAuthenticated: false }, action) =>
{
	switch (action.type) {
		case WAITLIST_FAILURE:
			return Object.assign({}, state, {
				isFetching: false,
				isAuthenticated: false,
				errorMessage: action.message
			});
		case WAITLIST_SUCCESS:
			return Object.assign({}, state, {
				isFetching: false,
				isAuthenticated: false,
				errorMessage: ''
			});
		case AUTH_SUCCESS:
			return Object.assign({}, state, {
				isFetching: false,
				isAuthenticated: true,
				errorMessage: ''
			});
		default:
			return state;
	}
};

export default landingReducer;



