import React, { Component, PropTypes } from 'react';

class Step3 extends Component {
  constructor(props){
    super(props);
    this.state = {
      type: 'p',
    };
  }

  handleChange(e) {
    this.setState({
      type: e.target.value,
    });
  }

  renderPounds() {
    let values = []
    for(let i = 110; i<330; i+=2){
      values.push(i);
    }
    return(
      <select onChange={() => this.props.onNext()}>
        { values.map((val) => {
          return(
              <option> {val} Pounds </option>
            );
          })
        }
      </select>
    )
  }

  renderKilogram() {
    let values = []
    for(let i = 50; i<150; i+=1){
      values.push(i);
    }
    return(
      <select onChange={() => this.props.onNext()}>
        { values.map((val) => {
          return(
              <option> {val} Kgs </option>
            );
          })
        }
      </select>
    )
  }
  render() {
    return(
      <div className='quiz-page__step quiz-page__step--3'>
        <span className='quiz-page__step__header header1--mobile'>What is your weight?</span>
        <div className='quiz-page__step__content quiz-page__step--3__content'>
          <div className='quiz-page__step__content__format'>
              <span className='text'>
                <input type='radio' checked={this.state.type === 'p'} value='p' onChange={e => this.handleChange(e)}/>
                Pounds
              </span>
              <span className='text'>
                <input type='radio' checked={this.state.type === 'k'} value='k' onChange={e => this.handleChange(e)}/>
                Kilograms
              </span>
          </div>
          <div className='quiz-page__step__content__select'>
            <div className='text--small--bold'>height</div>
            {
              this.state.type == 'p'?
                this.renderPounds()
                :
                this.renderKilogram()
            }
          </div>
        </div>
      </div>
    );
  }
}

Step3.propTypes = {
  onNext: PropTypes.object.isRequired
};

export default Step3;


