import React, { Component } from 'react';
import { Link } from 'react-router';



export default class LoginPage extends Component {

	constructor(props){
		super(props);
	}

	handleClickLogin () {
		console.log('Handle login!');
	}

	render() {
		return (
			<div className='login-page'>
					<div className='double-border' />
					<div className='login-page__container'>
						<div className='login-page__login'>
							<div>
								<div className='header1--mobile'>Welcome Back!</div>
								<div className='login-page__login__form form'>
									<span className='form__label'>Email Address</span>
									<input className='form__input' type='email'/>
									<span className='form__label'>Password</span>
									<input className='form__input' type='password'/>
								</div>
								<button className='button navigation-text button--line' onClick={() => this.handleClickLogin()}>Sign in</button>
								<Link to='/forgot' className='login-page__login__forgot'>Forgot your password?</Link>
							</div>
						</div>
						<div className='login-page__signup'>
							<div>
								<div className='header1--mobile'>New Customer?</div>
								<img src={require('../../images/quiz/quiz.svg')} alt='quiz'/>
								<span className='text'>Take our quiz to personalize your shopping preferences and to create an account. It only takes 30 seconds.</span>
								<Link to='/quiz' className='button button--gold navigation-text'>Get Started</Link>
							</div>	
						</div>
					</div>
			</div>
		);
	}
}




