import React, { Component, PropTypes } from 'react';

class AuthPage extends Component {
	constructor(props){
		super(props);
		this.state = {value: ''};

		this.handleChange = this.handleChange.bind(this);
		this.handleClickAuth = this.handleClickAuth.bind(this);
	}
	handleChange(event) {
		this.setState({value: event.target.value});
	}
	handleClickAuth() {
		const creds = { password: this.state.value };
		this.props.onAuthClick(creds);
	}
	render() {
		return(
			<div className='auth-page'>
				<div className='authheader'>
					<img src={require('../../images/Logo.png')} className='logo' alt='Couture'/>
					<div className='header0'>COUTURE</div>
				</div>
				<div className='content'>
					<span className='tagline'>Email us at admin@couture-store.com to enter.</span>
					<div className='form'>
						<span className='form__label'>Password:</span>
						<input className='form__input' type='password' value={this.state.value} onChange={this.handleChange}/>
						<button className='button navigation-text button--line' onClick={this.handleClickAuth}>Enter</button>       
					</div>
				</div>
			</div>

		);
	}
}


AuthPage.propTypes = {
	onAuthClick: PropTypes.func.isRequired
};

export default AuthPage;


