import React, { Component, PropTypes } from 'react';

class Step6 extends Component {
  constructor(props){
    super(props);
    this.state = {
      type: 'i',
    };
  }

  handleChange(e) {
    this.setState({
      type: e.target.value,
    });
  }

  renderInches(last) {
    let values = [];
    for(let i = 22; i<50; i+=2){
      values.push(i);
    }
    let func = () => {};
    if (last !== 0)
        func = this.props.onNext;

    return(
      <select onChange={() => func()}>
        {values.map((val) => {
          return(
              <option> {val} inches </option>
            );
          })
        }
      </select>
    );
  }

  renderCentimeters(last) {
    let values = [];
    for(let i = 50; i<130; i+=1){
      values.push(i);
    }
    let func = () => {};
    if (last !== 0)
        func = this.props.onNext;

    return(
      <select onChange={() => func()}>
        {values.map((val) => {
          return(
              <option> {val} cm </option>
            );
          })
        }
      </select>
    );
  }
  render() {
    return(
      <div className='quiz-page__step quiz-page__step--6'>
        <span className='quiz-page__step__header header1--mobile'>Please specify your body measurements</span>
        <div className='quiz-page__step__content quiz-page__step--6__content'>
          <div className='quiz-page__step__content__format'>
              <span className='text'>
                <input type='radio' checked={this.state.type === 'i'} value='i' onChange={e => this.handleChange(e)}/>
                Inches
              </span>
              <span className='text'>
                <input type='radio' checked={this.state.type === 'c'} value='c' onChange={e => this.handleChange(e)}/>
                Cms
              </span>
          </div>
          <div className='quiz-page__step--6__content--segment'>
          <div className='text--small--bold'>bust</div>
          <div className='text--small'>Place the measuring tape across your nipples and measure around the largest part of your chest.Be sure to keep the rope parallel to the floor.</div>
            {
              this.state.type == 'i'?
                this.renderInches(0)
                :
                this.renderCentimeters(0)
            }
          </div>
          <div className='quiz-page__step--6__content--segment'>
          <div className='text--small--bold'>waist</div>
          <div className='text--small'>Place the measuring tape about a 1/2 inch above your bellybutton (at the narrowest part of your waist) to measure around your torse. When measuring your waist, exhale and measure before inhaling again.</div>
            {
              this.state.type == 'i'?
                this.renderInches(0)
                :
                this.renderCentimeters(0)
            }
          </div>
          <div className='quiz-page__step--6__content--segment'>
          <div className='text--small--bold'>hips</div>
          <div className='text--small'>Place the measuring tape across the widest part of your hips/buttocks and measure all the way around while keeping the tape parallel to the floor.</div>          
            {
              this.state.type == 'i'?
                this.renderInches(1)
                :
                this.renderCentimeters(1)
            }
          </div>
          
        </div>
      </div>
    );
  }
}

Step6.propTypes = {
  onNext: PropTypes.object.isRequired
};

export default Step6;


