import React, { Component, PropTypes } from 'react';

class Step1 extends Component {
  constructor(props){
    super(props);
    this.state = {
      sizing: false,
    };
  }

  toggle() {
    this.setState({
      sizing: !this.state.sizing
    });
  }
  render() {
    return(
      <div className='quiz-page__step quiz-page__step--1'>
        <span className='quiz-page__step__header header1--mobile'>Please Select your size below. </span>
        <div className='quiz-page__step__content quiz-page__step--1__content'>
          <div className='quiz-page__step__row'>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>xxs</span>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>xs</span>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>s</span>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>m</span>
          </div>
          <div className='quiz-page__step__row'>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>l</span>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>xl</span>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>xxl</span>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>xxxl</span>
          </div>
          {
            !this.state.sizing?
               <span className='quiz-page__step--1__sizing text' onClick={() => this.toggle()}> See <span>sizing chart</span> for more info</span>
               :
              <span className='quiz-page__step--1__sizing text' onClick={() => this.toggle()}><span>Minimize</span> sizing chart</span>
          }
          {
            this.state.sizing?
              <img src={require('../../../images/quiz/sizingchart.png')} alt='sizing chart' />
              :
              null
          }
        </div>
      </div>
    );
  }
}

Step1.propTypes = {
  onNext: PropTypes.object.isRequired
};

export default Step1;


