import React, { Component } from 'react';
import { Link } from 'react-router';

class Step2 extends Component {
  constructor(props){
    super(props);
  }
  render() {
    return(
      <div className='quiz-page__step quiz-page__step--8'>
        <span className='quiz-page__step__header header1--mobile'>Finish your sign up!</span>
        <div className='quiz-page__step__content quiz-page__step--8__content'>
          <div className='form'>
            <span className='form__label'>Email Address</span>
            <input className='form__input' type='email'/>
            <div className='form--inline'>
              <div>
                <span className='form__label'>First Name</span>
                <input className='form__input' type='text'/>
              </div>
              <div className='form--inline--right'>
                <span className='form__label'>Last Name</span>
                <input className='form__input' type='text'/>
              </div>
              <div>
                <span className='form__label'>Password</span>
                <input className='form__input' type='password'/>
              </div>
              <div className='form--inline--right'>
                <span className='form__label'>Confirm Password</span>
                <input className='form__input' type='password'/>
              </div>
            </div>
          </div>
          <Link to='/' className='button navigation-text'>I'm finished</Link>          
        </div>
      </div>
    );
  }
}

export default Step2;


