import React, { Component, PropTypes } from 'react';

class Step2 extends Component {
  constructor(props){
    super(props);
    this.state = {
      type: 's',
    };
  }

  handleChange(e) {
    this.setState({
      type: e.target.value,
    });
  }

  renderStandard() {
    return (
      <div className='quiz-page__step__content__select--twin'>
        <select>
          <option> 3 feet </option>
          <option> 4 feet </option>
          <option> 5 feet </option>
          <option> 6 feet </option>
          <option> 7 feet </option>
        </select>
        <select onChange={() => this.props.onNext()}>
          <option> 0 inches </option>
          <option> 1 inches </option>
          <option> 2 inches </option>
          <option> 3 inches </option>
          <option> 4 inches </option>
          <option> 5 inches </option>
          <option> 6 inches </option>
          <option> 7 inches </option>
          <option> 8 inches </option>
          <option> 9 inches </option>
          <option> 10 inches </option>
          <option> 11 inches </option> 
        </select>
      </div>
    );
  }

  renderMetric() {
    let values = [];
    for(let i = 92; i<214; i+=2){
      values.push(i);
    }
    return(
      <select onChange={() => this.props.onNext()}>
        {values.map((val) => {
          return(
              <option> {val} cms </option>
            );
          })
        }
      </select>
    );
  }
  render() {
    return(
      <div className='quiz-page__step quiz-page__step--2'>
        <span className='quiz-page__step__header header1--mobile'>What is your height?</span>
        <div className='quiz-page__step__content quiz-page__step--2__content'>
          <div className='quiz-page__step__content__format'>
              <span className='text'>
                <input type='radio' checked={this.state.type === 's'} value='s' onChange={e => this.handleChange(e)}/>
                Standard
              </span>
              <span className='text'>
                <input type='radio' checked={this.state.type === 'm'} value='m' onChange={e => this.handleChange(e)}/>
                Metric
              </span>
          </div>
          <div className='quiz-page__step__content__select'>
            <div className='text--small--bold'>height</div>
            {
              this.state.type == 's'?
                this.renderStandard()
                :
                this.renderMetric()
            }
          </div>
        </div>
      </div>
    );
  }
}

Step2.propTypes = {
  onNext: PropTypes.object.isRequired
};

export default Step2;


