import React, { Component, PropTypes } from 'react';

class Step7 extends Component {
  constructor(props){
    super(props);
  }
  render() {
    return(
      <div className='quiz-page__step quiz-page__step--7'>
        <span className='quiz-page__step__header header1--mobile'>Which style would best describe your
        personal aesthetic?</span>
        <div className='quiz-page__step__content quiz-page__step--7__content'>
          <div className='quiz-page__step__row'>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>rock</span>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>preppy</span>
          </div>
          <div className='quiz-page__step__row'>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>streetwear</span>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>elegant</span>
          </div>
          <div className='quiz-page__step__row'>
            <span className='text--small--bold' onClick={() => this.props.onNext()}>None of the above</span>
          </div>
        </div>
      </div>
    );
  }
}

Step7.propTypes = {
  onNext: PropTypes.object.isRequired
};

export default Step7;


