import React, { Component } from 'react';
import { Link } from 'react-router';

export default class Header extends Component
{
	render() {
		return (
			<div id='header' className='header'>
				<div className='header__container'>
				<Link to='/' className='header__logo'>
					<span>
						<img src={require('../../images/Logo.png')} alt='Couture'/>
						<span className='navigation-text'>COUTURE</span>
					</span> 
				</Link>
				<div className='header__menu header__menu--left'>
					<Link to='/browse' className='navigation-text'>Browse</Link>
					<Link to='/' className='navigation-text'>Brands</Link>
					<Link to='/' className='navigation-text'>What is Couture?</Link>
					<Link to='/' className='navigation-text'>Our Story</Link>
				</div>
				<div className='header__menu header__menu--right'>
					<Link to='/login' className='navigation-text'>Sign in</Link>
					<Link to='/order' className='navigation-text'><img className='header__menu--basket-icon' src={require('../../images/shopping_icon.svg')} alt='basket' /><span>(2)</span></Link>
				</div>
				</div>
			</div>
		);
	}
}




