import React, { PropTypes } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';

const App = (props) => {
	return (
		<div>
			<Header />
			{props.children}
			<Footer />
		</div>
	);
};

App.propTypes = {
	children: PropTypes.element
};

export default App;



