import React, { Component, PropTypes } from 'react';

class Step5 extends Component {
  constructor(props){
    super(props);
    this.state = {
      sizing: false,
    };
  }

  toggle() {
    this.setState({
      sizing: !this.state.sizing
    });
  }
  render() {
    return(
      <div className='quiz-page__step quiz-page__step--5'>
        <span className='quiz-page__step__header header1--mobile'>Which image best represents your waist  level?</span>
        <div className='quiz-page__step__content'>
        <div className='quiz-page__step__row'>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>High</span>
          <img src={require('../../../images/quiz/low.svg')} alt='high' />
          </div>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>Medium</span>
          <img src={require('../../../images/quiz/low.svg')} alt='medium' />
          </div>
          <div onClick={() => this.props.onNext()}><span className='text--small--bold'>Low</span>
          <img src={require('../../../images/quiz/low.svg')} alt='low' />
          </div>
        </div>
        </div>
      </div>
    );
  }
}

Step5.propTypes = {
  onNext: PropTypes.object.isRequired
};

export default Step5;


