import React, { Component } from 'react';
import { Link } from 'react-router';

export default class Footer extends Component
{
	render() {
		return (
			<div className='footer'>
				<div className='footer__container'>
          <div className='footer__menu footer__menu--left'>
            <Link to='/' className='navigation-text'>What is Couture?</Link>
            <Link to='/' className='navigation-text'>Brands</Link>
            <Link to='/' className='navigation-text'>Our Story</Link>
          </div>
          <div className='footer__menu footer__menu--center'>
            <div className='footer__menu--center__logos'>
              <a href='/' target='_blank'><img src={require('../../images/social/Facebook.svg')} alt='Facebook' /></a>
              <a href='/' target='_blank'><img src={require('../../images/social/Twitter.svg')} alt='Twitter' /></a>
              <a href='/' target='_blank'><img src={require('../../images/social/Instagram.svg')} alt='Instagram' /></a>
              <a href='/' target='_blank'><img src={require('../../images/social/Pinterest.svg')} alt='Pinterest' /></a>
            </div>
            <div className='footer__menu--center__links'>
              <Link to='/' className='navigation-text'>Contact</Link>
              <Link to='/' className='navigation-text'>Help</Link>
              <Link to='/' className='navigation-text'>FAQ</Link>
            </div>
          </div>
          <div className='footer__menu footer__menu--right'>
            <Link to='/' className='navigation-text'>Browse</Link>
            <Link to='/login' className='navigation-text'>Sign in</Link>
            <Link to='/quiz' className='navigation-text'>Take Our Quiz</Link>
          </div>
          <div className='footer__copyright navigation-text'>© Couture 2017. See our Privacy Policy and Terms of Use.</div>
				</div>
			</div>
		);
	}
}




